import React, { Component } from "react";
import '../static/styles/mask.scss'
let loadingImg=require('../static/images/loading2.png');
export default class Loader extends Component{
    constructor(props){
        super(props)
    }
    render(){
        return(
            <div className="loading-mask">
                <img className="loading-img" src={loadingImg} alt=""/>
                {this.props.txt ? <div className="loader-txt">{this.props.txt}</div> : null}
            </div>
        )
    }
}