import axios from 'axios'
// import 'babel-polyfill'
// import createHistory from 'history/createBrowserHistory'
// let history=createHistory();
// const BASE_URL='http://192.168.31.204:8989'
// const BASE_URL='/dev/api'
// const BASE_URL='http://rzlt.genwoshua.com/';
const BASE_URL='exp/api/';
// http://rzlt.genwoshua.com/wx/lite/login?code=__test2
// classx.cn/exp/api/main/index/load
import URL from './static/api'
const XHR=(name,jsondata,history)=>{
    if(URL[name]){
        if(URL[name].method=='post'){
            return new Promise((resolve, reject)=>{
                REQUESTS.post(URL[name].url,jsondata)
                    .then(res=>{
                        if(res.data){
                            resolve(res.data)
                        }

                    })
                    .catch(err=>{
                        if(err.response){
                            if(err.response.status==403){//未登录
                                // window.location.href='/user/auth';
                                history.replace({
                                    pathname:'/login'
                                })
                                // history.replace({
                                //     pathname:'/user/auth'
                                // })
                                return
                            }else{
                                reject({'message':err.response.data.message})
                            }
                        }else if(err.request){
                            console.log(err.request);
                        }else{
                            console.log('Error', err.message);
                        }
                        reject({'message':'你的请求出错啦'})
                    })

            })
        }else if(URL[name].method=='get'){
            return new Promise((resolve, reject)=>{
                let requestUrl=URL[name].url;
                let requestData=jsondata;
                if(jsondata.urlParam){
                    requestUrl=requestUrl+jsondata.urlParam;
                    requestData={};
                }
                REQUESTS.get(requestUrl,requestData)
                    .then(res=>{
                        if(res.data){
                            resolve(res.data)
                        }

                    })
                    .catch(err=>{
                        if(err.response){
                            console.log(err.response)
                            if(err.response.status==403){//未登录
                                // window.location.href='/user/auth';
                                history.replace({
                                    pathname:'/login'
                                })
                                // history.replace({
                                //     pathname:'/user/auth'
                                // })
                                return
                            }else{
                                reject({'message':err.response.data.message})
                            }
                        }else if(err.request){
                            console.log(err.request);
                        }else{
                            console.log('Error', err.message);
                        }
                        reject({'message':'你的请求出错啦'})
                    })

            })
        }
    }else{
        return false
    }
};
const REQUESTS={
    post:function(url,jsondata){
        return axios.post(BASE_URL+url,jsondata)
    },
    get:function(url,jsondata) {
        return axios.get(BASE_URL+url,{params:jsondata})
    }
};
export default XHR