import React, { Component } from "react";
import "../static/styles/home.scss";
import AppCard from '../components/AppCard'
import Rscroller from '../components/Rscroller'
import PUBLIC from '../public'
import XHR from '../XHR'
import Loader from '../components/loading'
import RefreshBtn from '../components/RefreshBtn'
let clipboard=null;
export default class Home extends Component {
    constructor(props) {
        super(props);

        this.state = {
            isLoading: true,
            list:[1,2,3,4,56,5],
            loaded:false,
            showAppCode:false,
            needLoader:true,
            products:[],
            touchStartTime:0,
            touchEndTime:0,
            query: {
                size: 5,
                from: null,
                priority: null,
            },
            loadMore:false,
            loading:true,
            last:false,
            showRefresh:false,
            notListenScroll:false
        };
        this.enterApp=this.enterApp.bind(this);
        this.stopBubble=this.stopBubble.bind(this);
        this.hideMask=this.hideMask.bind(this);
        this.showHandleBook=this.showHandleBook.bind(this);
        this.hideHandleBook=this.hideHandleBook.bind(this);
        this.loadData=this.loadData.bind(this)
        this.loadMoreData=this.loadMoreData.bind(this)
        this.touchStart=this.touchStart.bind(this)
        this.touchEnd=this.touchEnd.bind(this)
        this.refreshPage=this.refreshPage.bind(this)
    }

    async componentDidMount() {
        this.setState({ isLoading: false });
        console.log('组件加载---')
        console.log(Date.now());
        clipboard = new ClipboardJS('.copy-btn');
        clipboard.on('success', function(e) {
            // console.info('Action:', e.action);
            // console.info('Text:', e.text);
            // console.info('Trigger:', e.trigger);
            alert('复制成功');
            e.clearSelection();
        });

        clipboard.on('error', function(e) {
            alert('复制失败');
            // console.error('Action:', e.action);
            // console.error('Trigger:', e.trigger);
        });
        this.loadData({query:this.state.query},false,false)
    }
    componentWillUnmount(){
        clipboard=null;
        // console.log('组件销毁---------')
        // console.log(Date.now())
        if(this.timeOut){
            clearTimeout(this.timeOut);
            this.timeOut=null;
        }
    }
    loadData(requestObj,isLoadMore,isRefresh){
        XHR('indexData',requestObj,this.props.history)
            .then(res=>{
                if(res.success && res.products){
                    // this.setState({
                    //     products:res.products,
                    //     loading:false
                    // });
                    let products=res.products;
                    if(isLoadMore){
                        products=this.state.products.concat(products)
                    }
                    this.setState(prevState=>({
                        products:products,
                        loading:false,
                        loadMore:false,
                        last:res.products.length<this.state.query.size,
                        loaded:true,
                        showRefresh:false,
                        query:res.products.length>0 ? Object.assign({},prevState.query,{from:res.products[res.products.length-1].createTime}) : prevState.query
                    }));
                    if(this.state.notListenScroll){
                        window.scrollTo(0,0);
                    }
                    this.timeOut=setTimeout(()=>{
                        this.setState({
                            notListenScroll:false
                        })
                    },1000)
                }
            })
            .catch((err)=>{
                this.setState({
                    loading:false,
                    loadMore:false,
                    showRefresh:false,
                    notListenScroll:false
                })
            })
    }
    refreshPage(){
        this.setState({
            showRefresh:true,
            notListenScroll:true
        },()=>{
            this.loadData({query:{
                size: 5,
                from: null,
                priority: null}
            },false,true)
        });

    }
    enterApp(e){
        this.setState({
            showAppCode:true
        })
    }
    stopBubble(e){
        if (e.stopPropagation) {
            e.stopPropagation()
        } else {
            e.cancelBubble = true;
        }
    }
    hideMask(){
        this.setState({
            showAppCode:false
        })
    }
    showHandleBook(){
        this.setState({
            showHandleBook:true
        })
    }
    hideHandleBook(){
        this.setState({
            showHandleBook:false
        })
    }
    loadMoreData(){
        if(!this.state.last){
            this.setState({
                loadMore:true
            });
            this.loadData({query:this.state.query},true,false)
        }
    }
    touchStart(){
        console.log('开始触摸-------')
        console.log(Date.now());
        this.setState({
            touchStartTime:Date.now()
        })
    }
    touchEnd(){
        console.log('触摸结束-------')
        console.log(Date.now());
        this.setState({
            touchEndTime:Date.now()
        },()=>{
            let touchInsistSecond=(this.state.touchEndTime-this.state.touchStartTime)/1000;
            if(touchInsistSecond>=2){
                console.log('用户长按')
            }else{
                console.log('用户没有长按')
            }
        })

    }
    renderLander() {
        return (
            <div className="home-page">
                {this.state.loading ? <Loader txt={'加载中'}></Loader> :
                    <Rscroller
                        loadMore={!this.state.last}
                        loading={this.state.loadMore}
                        loadMoreData={this.loadMoreData}
                        needLoader={this.state.needLoader}
                        loaded={this.state.loaded}
                        noEmpty={this.state.products.length>0}
                        notListenScroll={this.state.notListenScroll}
                    >
                        <RefreshBtn
                            refreshPage={this.refreshPage}
                        />
                        {this.state.showRefresh ?
                            <Loader txt={'加载中'}></Loader> : null
                        }
                        <div className="page-header">
                            <h1 className="page-h1">
                                <span>Today<span className="header-txt" style={{fontWeight:'bold',marginLeft:5}}>新品</span></span>
                                <span className="header-txt" onClick={this.showHandleBook}>使用帮助<span className="qs-symbol">?</span></span>
                            </h1>
                        </div>
                        <div className="card-list">
                            {this.state.products.map((item,index)=>
                                <div className="card-item" key={'home'+index}>
                                    <AppCard
                                        appName={item.name}
                                        appIntro={item.intro}
                                        playIntro={item.task}
                                        index={index}
                                        imgurl={item.images[0].url+PUBLIC.cropImg(60,60)}
                                        enterApp={this.enterApp}
                                    />
                                </div>
                            )}
                        </div>
                        {this.state.showAppCode ?
                            <div className="modal-mask" onClick={this.hideMask}>
                                <div className="img-content" onClick={this.stopBubble}>
                                    <img onTouchStart={this.touchStart} onTouchEnd={this.touchEnd} className="app-code-img" src="http://cdn.genwoshua.com/yearQQR/05147c7bd5424a2283798019273921be" alt=""/>
                                    <div className="app-code-txt">长按识别二维码，开始体验</div>
                                </div>
                            </div> : null
                        }
                        {this.state.showHandleBook ?
                            <div className="modal-mask" onClick={this.hideHandleBook}>
                                <div className="use-handlebook" onClick={this.stopBubble}>
                                    <div className="rule-intro">
                                        <div className="rule-title">规则说明</div>
                                        <div className="rule-item">
                                            <div className="rule-name">什么是体验师</div>
                                            <div className="rule-desc">作为种子用户，体验厂商的新产品，帮助厂商做得更好</div>
                                        </div>
                                        <div className="rule-item">
                                            <div className="rule-name">如何体验产品</div>
                                            <div className="rule-desc">点击列表中的产品卡片，按说明体验，之后回到此页面</div>
                                        </div>
                                        <div className="rule-item">
                                            <div className="rule-name">特别说明</div>
                                            <div className="rule-desc">部分Android手机在内存不足时，会在体验产品后直接返回到微信聊天界面（跳过本页面），若发生请忽略该产品</div>
                                        </div>
                                    </div>
                                    <div className="wx-service">点击复制客服微信号:XXxx77888888</div>
                                </div>
                            </div> : null
                        }
                    </Rscroller>
                }

            </div>
        );
    }
    render() {
        return (
            this.renderLander()
        );
    }
}