import React, {Component} from 'react';
import {render} from 'react-dom';
import '../static/styles/appcard.scss'
export default class AppCard extends Component {
    constructor(props){
        super(props)
    }
    render(){
        return(
            <div className="single-card">
                <div className="card-header">
                    <img className="appcard-avatar" src={this.props.imgurl} alt=""/>
                    <div>
                        <div className="app-name">{this.props.appName}</div>
                        <div className="app-intro">{this.props.appIntro}</div>
                    </div>
                    <div className="play-btn" onClick={()=>this.props.enterApp(this.props.index)}>立即体验</div>
                </div>
                <div className="play-intro">{this.props.playIntro}</div>
            </div>
            )
    }
}