import React,{Component} from 'react';
import '../static/styles/loader.scss';
export default class Loader extends Component{
    constructor(props){
        super(props);
    }
    render(){
        let realLoader='';
        if(this.props.loadMore && !this.props.loading && this.props.loaded){
            realLoader=<div style={{height:50,textAlign:'center',lineHeight:'50px',color:'#bbbbbb',fontSize:12}}>上拉加载更多</div>
        }else if(this.props.loadMore && this.props.loading){
            // realLoader=<img className="loaderImg" src="/static/images/refreshing.png" alt="" />
            realLoader= <div style={{height:50,textAlign:'center',lineHeight:'50px',color:'#bbbbbb',fontSize:12}}>{this.props.endmsg ? this.props.endmsg : '加载中'}</div>
        }else if(!this.props.loadMore && !this.props.loading && this.props.noEmpty){
            realLoader= <div style={{height:50,textAlign:'center',lineHeight:'50px',color:'#bbbbbb',fontSize:12}}>{this.props.endmsg ? this.props.endmsg : '没有更多啦'}</div>
        }
        return(
            <div className="bottomloader" id="pageBottomLoader">
                {realLoader}
            </div>
        )
    }
}