import React, {Component} from 'react';
// import './App.css';
import Routes from "./Routes";
import {withRouter} from 'react-router-dom'//这种做法的弊端是,想在redux中使用history的话,需要手动将history传递过去.但是仍然推荐使用这种,方便控制所有页面权限
import {  Switch,Route } from "react-router-dom";

import XHR from './XHR'
import PUBLIC from './public'
import './app.scss'
import Tabbar from './components/Tabbar';
let tabImgList=[
    {
        active:require('./static/images/lite_index-active.png'),
        normal:require('./static/images/lite_index.png'),
        name:'新品',
        link:'/'
    },
    {
        active:require('./static/images/lite_index-active.png'),
        normal:require('./static/images/lite_index.png'),
        name:'发现',
        link:'/more'
    },
    {
        active:require('./static/images/lite_mine-active.png'),
        normal:require('./static/images/lite_mine.png'),
        name:'账户',
        link:'/mine'
    }
];
import asyncComponent from './components/AsyncComponent'
import Loader from './components/loading'
// import HomePage from './containers/Home'
// import RankPage from './containers/Rank'
// import MinePage from './containers/Mine'
// import MorePage from './containers/More'
let HomePage=asyncComponent(()=>import ('./containers/Home'));
let RankPage=asyncComponent(()=>import ('./containers/Rank'));
let MinePage=asyncComponent(()=>import ('./containers/Mine'));
let MorePage=asyncComponent(()=>import ('./containers/More'));
class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isAuthenticated: false,
            isAuthenticating: false,
            activeTab:1,
            tabShow:true,
            auth:false
        };
        this.changeTab=this.changeTab.bind(this);
        this.noticeTab=this.noticeTab.bind(this);
        this.countRouter=this.countRouter.bind(this);
        this.refreshPage=this.refreshPage.bind(this);
    }
    componentDidMount(){


        console.log('最外层组件加载完毕-----------');
        console.log(Date.now()+'---------------');
        XHR('login',{code:'debug'},this.props.history)
            .then(res=>{
                this.setState({
                    auth:true
                })
            }).catch((err)=>{
                console.log(err)
        });
        let pathName=this.props.location.pathname;
        this.countRouter(pathName);
        this.props.history.listen((newRouter) => {
            this.countRouter(newRouter.pathname)
        })
    }
    countRouter(pathName){
        let tabShow=false;
        let activeTab=1;
        switch(pathName){
            case '/':
                activeTab=1;
                tabShow=true;
                break;
            case '/more':
                activeTab=2;
                tabShow=true;
                break;
            case '/mine':
                activeTab=3;
                tabShow=true;
                break;
        }
        this.setState({
            activeTab:activeTab,
            tabShow:tabShow
        });
    }
    changeTab(e,link){
        this.setState({
            activeTab:e
        }
        ,()=>{
            this.props.history.replace(link)
        }
        );

    }
    noticeTab(boolean){
        console.log(boolean)
    }
    refreshPage(){
        console.log(this.state.activeTab)
    }
    render() {
        return (
            <div className="App">
                {!this.state.auth ? <Loader txt={'登录中'}></Loader> :
                    <Switch>
                        <Route
                            path="/"
                            exact
                            component={HomePage}
                        />
                        <Route
                            path="/more"
                            exact
                            component={MorePage}
                        />
                        <Route
                            path="/mine"
                            exact
                            component={MinePage}
                        />
                        <Route
                            path="/rank"
                            exact
                            component={RankPage}
                        />

                    </Switch>}
                {this.state.tabShow && this.state.auth ?
                    <div className="bottom-fixed">
                        <Tabbar activetab={this.state.activeTab} tabs={tabImgList} changeTab={this.changeTab}/>
                    </div> : null
                }
            </div>
        );
    }
}

export default withRouter(App);
