const AsyncNewNote = import("./containers/NewNote");
export default ({ childProps }) =>
    <Switch>
        <Route
            path="/"
            exact
            component={AsyncHome}
            props={childProps}
        />
        <Route
            path="/mine"
            exact
            component={AsyncLogin}
            props={childProps}
        />
        <Route
            path="/signup"
            exact
            component={AsyncSignup}
            props={childProps}
        />
        <Route
            path="/notes/new"
            exact
            component={AsyncNewNote}
            props={childProps}
        />

    </Switch>
;