import React ,{Component} from 'react'
import Loader from './Loader'
import '../static/styles/rscroll.scss'
let contentHeight=0;
export default class Rscroller extends Component{
    constructor(props){
        super(props);
        this.state={
            refreshing:false,
            opacity:1,
        };
        this.onScroll=this.onScroll.bind(this);
        this.refreshScroll= this.refreshScroll.bind(this)
    }
    componentDidMount(){
        window.addEventListener('scroll',this.onScroll,false);
        contentHeight=this.refs.Rscroller.offsetHeight;
    }
    componentWillUnmount(){
        window.removeEventListener('scroll',this.onScroll,false)
    }
    refreshScroll(){
        let scrollTop=document.documentElement.scrollTop || document.body.scrollTop;
        if(contentHeight!=this.refs.Rscroller.offsetHeight){
            contentHeight=this.refs.Rscroller.offsetHeight
        }
        if(scrollTop>0 && scrollTop>contentHeight-window.innerHeight-100){
            if(!this.props.loading && this.props.loadMore){
                this.props.loadMoreData()
            }
        }
    }
    onScroll(){
        if(!this.props.notListenScroll){
            this.refreshScroll();
        }

    }
    render(){
        return(
            <div className="R-inifinite-scroll" ref="Rscroller">
                <div>
                    <div className="Rscroller-content">
                        {this.props.children}
                    </div>
                    {this.props.noLoader ? null :
                        <div style={{opacity:this.props.needLoader ? 1 : 0}}>
                            <Loader
                                loadMore={this.props.loadMore}
                                loading={this.props.loading}
                                noEmpty={this.props.noEmpty}
                                loaded={this.props.loaded}
                            />
                        </div>
                    }

                </div>
            </div>
        )
    }
}
