import React, {Component} from 'react';
import {render} from 'react-dom';
// import {Link} from 'react-router-dom'
import '../static/styles/tabbar.scss';
let tabImgList=[
    {active:require('../static/images/lite_index-active.png'),normal:require('../static/images/lite_index.png')},
    {active:require('../static/images/lite_zxyyh.png'),normal:require('../static/images/lite_zxyyhui.png')},
    {active:require('../static/images/lite_mine-active.png'),normal:require('../static/images/lite_mine.png')}
];
export default class Tabbar extends Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div className="tabBar-container">
                {this.props.tabs.map((item,index)=>
                    <div className="tab-item" key={index}>
                        <span className="single-atag" onClick={() => {
                            this.props.changeTab(index+1,item.link)
                        }}>
                         <img alt="" src={this.props.activetab === index+1 ? this.props.tabs[index].active : this.props.tabs[index].normal} className="tab-img"/>
                        <span className="tab-name">{item.name}</span>
                        </span>
                    </div>
                )}
            </div>
        )
    }
}
