import React, { Component } from "react";
import RefreshBtn from '../components/RefreshBtn'

import "../static/styles/mine.scss";
import XHR from '../XHR'
import Loader from '../components/loading'

export default class Login extends Component {
    constructor(props) {
        super(props);

        this.state = {
            loading: true,
            email: "",
            password: "",
            query: {
                keywords: '',
                from: null,
                size: 20
            },
            settingData:{},
            notListenScroll:false
        };
        this.goRank=this.goRank.bind(this)
        this.loadData=this.loadData.bind(this)
        this.refreshPage=this.refreshPage.bind(this)
    }

    async componentDidMount(){
        this.loadData({query:this.state.query})
        // console.log(history)
    }
    goRank(){
        this.props.history.push('/rank')
    }
    loadData(requestObj){
        XHR('settingData',requestObj,this.props.history)
            .then(res=>{
                this.setState({
                    settingData:res,
                    loading:false,
                    showRefresh:false
                });

            })
            .catch(err=>{
                this.setState({
                    loading:false,
                    showRefresh:false
                });
            })
    }
    refreshPage(){
        this.setState({
            showRefresh:true,
        },()=>{
            this.loadData({query:{
                keywords: '',
                from: null,
                size: 20}
            })
        });
    }
    render() {
        return (
            <div className="page-mine">
                {/*<div className="copy-container">*/}
                    {/*<div className="single-btn copy-btn" data-clipboard-text="Just because you can doesn't mean you should — clipboard.js">复制文案</div>*/}
                    {/*<div className="single-btn share-btn" data-clipboard-text="Just because you can doesn't mean you should — clipboard.js">二维码收徒</div>*/}
                    {/*<div className="single-btn link-btn" data-clipboard-text="Just because you can doesn't mean you should — clipboard.js">复制链接</div>*/}

                {/*</div>*/}
                {this.state.loading ?
                    <Loader txt={'加载中'}></Loader> :
                    <div>
                        <RefreshBtn
                            refreshPage={this.refreshPage}
                        />
                        {this.state.showRefresh ?
                            <Loader txt={'加载中'}></Loader> : null
                        }
                        <div className="page-part">
                            <div className="mine-coin">我的体验币</div>
                            <div className="coin-amount">{this.state.settingData.user.money}</div>
                            <div className="exchange-btn">兑换</div>
                            <div className="rank-list" onClick={this.goRank}>排行榜</div>
                        </div>
                        <div className="page-part">
                            <div className="rank-list">如喜欢本软件，可以分享给朋友</div>
                            <div className="exchange-btn" style={{width:190,marginTop:10}}>邀请朋友一起玩</div>
                            <div className="mine-coin" style={{marginTop:15}}>已有<span className="big-zero">{this.state.settingData.user.shared}</span>位朋友成为了体验师</div>
                        </div>
                        <div className="page-part">
                            <div className="rank-list">累计已获体验币{this.state.settingData.user.totalMoney}</div>
                        </div>
                    </div>
                }
            </div>
        );
    }
}